

.header {
    width: 400px;
    display: flex;

    transform: translate(-255px);
    transition-duration: 500ms;
}

.header:hover {
    transform: translate(0);
    transition-duration: 500ms;
}

.link {
    text-decoration: none;
    margin: 10px;

    color: greenyellow;
    font-family: BlinkMacSystemFont;
}

.active{

    text-decoration: none;
    margin: 10px;

    font-weight: bold;
    color: green;
    font-size: 24px;
    font-family: BlinkMacSystemFont;
}

.block {
    margin: 10px;
}
.img {
     height: 30px;
     width: 30px;



 }